import TagSexAge from '../common/tag-sex-age.vue'

export default {
	components: {
		TagSexAge
	},
	props: {
		item: Object,
		index: Number
	},
	data() {
		return {
			isguanzhu: this.item.isguanzhu
		}
	},
	methods: {
		guanzhu() {
			this.isguanzhu = true;
			uni.showToast({
				title: '关注成功'
			});
		},
		imgDetail(index) {
			// 预览图片
			uni.previewImage({
				urls: this.item.morepic,
				current: index
			})
		}
	}
}
